import { useDisclosure } from '@chakra-ui/hooks'
import { Drawer, DrawerBody, DrawerContent, DrawerOverlay } from '@chakra-ui/modal'
import { lazy, Suspense, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { updateSelectedCollectionId } from './state/collection/reducer'

const Collection = lazy(() => import('./widgets/Collection/collection'))

function App() {
  const selectedCollectionId = useSelector((state) => state.collection.selectedCollectionId)
  const collectionId = 511
  const dispatch = useDispatch()
  const { isOpen, onOpen, onClose } = useDisclosure()
  const closeDrawer = () => {
    onClose()
    dispatch(updateSelectedCollectionId({ id: null }))
  }

  useEffect(() => {
    if (selectedCollectionId) {
      onOpen()
    }
  }, [onOpen, selectedCollectionId])

  // if (!selectedCollectionId) return null
  return (
    <div className="App">
      <Suspense fallback={null}>
        <Drawer placement={'bottom'} onClose={onClose} isOpen={isOpen} size={'xs'}>
          <DrawerOverlay bg="rgba(0,0,0,0.8)" />
          <DrawerContent h="96%" bg="transparent">
            <DrawerBody p="0" borderTopRightRadius={'20'} borderTopLeftRadius={'20'} bg="background">
              <Collection
                accessKey="b854627c46904a5db26fdbe920a8402d"
                collectionId={selectedCollectionId}
                closeDrawer={closeDrawer}
              />
            </DrawerBody>
          </DrawerContent>
        </Drawer>
      </Suspense>
    </div>
  )
}

export default App
